<template>
    <main class="container content">
        
        <div id="pie" style="width:500px;height:500px;"></div>
    </main>
</template>
<script>
    import echarts from 'echarts'
    export default {
        data() {
            return {
                // 
                chart: null,
                // 
                head: ['','','','',''],
                headData: [ {value:335, name:''},
                    {value:310, name:''},
                    {value:234, name:''},
                    {value:135, name:''},
                    {value:1548, name:''}
                ]
            }
        },
        methods: {
            // 
            drawPie(id) {
                // 
                this.chart = echarts.init(document.getElementById(id));
                // 
                this.chart.showLoading();
                // data
                let that = this;
                // set
                this.chart.setOption({
                    title : {
                        text: '',
                        subtext: '',
                        x:'center'
                    },
                    tooltip : {
                        trigger: 'item',
                        formatter: "{a} <br/>{b} : {c} ({d}%)"
                    },
                    legend: {
                        orient: 'vertical',
                        left: 'left',
                        data: that.head
                    },
                    series : [
                        {
                            name: '',
                            type: 'pie',
                            radius : '55%',
                            center: ['50%', '60%'],
                            data:that.headData,
                            itemStyle: {
                                emphasis: {
                                    shadowBlur: 10,
                                    shadowOffsetX: 0,
                                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                                }
                            }
                        }
                    ]
                });
                this.chart.hideLoading()
            }
        },
        beforeCreate(){

        },
        mounted(){
            //this.headData[4].value=10000;
            this.drawPie('pie');
        }
    }
</script>


