<template>
    <main id="importPlayer" class="container">
        <div class="addMem">
            <header class="head-title">
                <p>
                    <span></span>
                </p>
            </header>
            <div style="margin-right:20px;text-align:right">
                <a target="_blank" href="http://ms.yunbisai.com/demo/.xls" class="text-blue"></a>
                <i class="el-icon-delete error" v-show="allMemberInfo.length" @click="clearData"></i>
                <el-upload :http-request="uploadExcel"
                           action="aa"
                           :show-file-list="false"
                           style="display:inline-block;">
                    <el-button type="primary"> excel</el-button>
                </el-upload>
                <el-button type="success" :disabled="!allMemberInfo.length || isUpload" :loading="isUpload" @click="addAllExcelData"></el-button>
            </div>
            <p class="money">
                <!--<span class="text-red">{{money.toFixed(2)}} </span> <span class="tip">{{signFee}}/</span>-->
                <span class="totalNum">{{allMemberInfo.length}}{{rightNum}}</span>
                <!--<a href="" download="demo.xls"></a>-->
            </p>
            <p class="tip"></p>
            <section class="add-area">
                <el-table ref="multipleTable" :data="allMemberInfo" stripe :loading="isAjax">
                    <el-table-column prop="no" label="" min-width="60"></el-table-column>
                    <el-table-column label="" width="190">
                        <template scope="scope">
                            {{ scope.row.ID }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="name" label="" min-width="120"></el-table-column>
                    <el-table-column prop="rank" label="" min-width="90">
                        <template scope="scope">
                            {{ getRankName(scope.row.rank) }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="isFee" label="/" min-width="120">
                        <template scope="scope">
                            {{ scope.row.isFee==='1' || scope.row.isFee==='0' ? ( scope.row.isFee * signFee ).toFixed(2) : scope.row.isFee }}
                        </template>
                    </el-table-column>
                    <el-table-column prop="time" label="" min-width="160"></el-table-column>
                    <el-table-column label="" min-width="160">
                        <template scope="scope">
                            <el-tag v-if="scope.row.state===2" type="danger"></el-tag>
                            <el-tag v-if="scope.row.state===1" type="warning"></el-tag>
                            <el-tag v-if="scope.row.state===0" type="success"></el-tag>
                        </template>
                        <!--<template scope="scope">-->
                            <!--<el-button type="text" @click="watch(scope.row,scope.row.id,scope.row.time)"></el-button>-->
                            <!--<el-popover-->
                                    <!--placement="top"-->
                                    <!--title=""-->
                                    <!--width="200"-->
                                    <!--v-model="scope.row.del"-->
                                    <!--v-if="$store.state.total.mem.child.memDel[1]">-->
                                <!--<p></p>-->
                                <!--<div style="text-align: right; margin: 0">-->
                                    <!--<el-button size="mini" type="text" @click="scope.row.del = false"></el-button>-->
                                    <!--<el-button type="primary" size="mini" :disabled="tinyAjax" :loading="tinyAjax" @click="scope.row.del = del(scope.row.id)"></el-button>-->
                                <!--</div>-->
                                <!--<el-button type="text" class="text-red" slot="reference"></el-button>-->
                            <!--</el-popover>-->
                        <!--</template>-->
                    </el-table-column>
                </el-table>
                <p class="tip"></p>
            </section>
        </div>
        <!--  -->
        <el-dialog
                v-model="uploadStatus"
                title=""
                size="tiny"
                :close-on-click-modal="false"
                :close-on-press-escape="false">
            <div class="text-center">
                <el-progress
                        type="circle"
                        :percentage="rightNum=== 0 ? 100 : parseInt( (successNum + errorNum + warningNum) / rightNum * 100 )"
                        :status="(successNum+errorNum+warningNum) >= rightNum ? 'success' : '' ">
                </el-progress>
                <p>
                    <span class="success">{{successNum}}</span>
                </p>
                <p>
                    <span class="warning">{{warningNum}}</span>
                </p>
                <p>
                    <span class="error">{{errorNum}}</span>
                </p>
                <div style="margin-top:10px"></div>
                <p class="tip"></p>
                <div style="margin-top:10px"></div>
                <el-button @click="stay" v-show="parseInt(successNum+errorNum+warningNum) >= rightNum" :disabled="parseInt(successNum+errorNum+warningNum) < rightNum"></el-button>
                <el-button type="primary" @click="toAddPlayer" v-show="parseInt(successNum+errorNum+warningNum) >= rightNum" :disabled="parseInt(successNum+errorNum+warningNum) < rightNum"></el-button>
            </div>
        </el-dialog>
        <el-dialog v-model="loadVis" title="" type="tiny">
            <p style="font-size:16px"> <span class="error"> {{maxlen}} </span>{{lenMessage}}</p>
            <div style="margin-top:10px"></div>
            <div class="text-center">
                <el-button size="small" @click="continueLoad"></el-button>
                <el-button size="small" type="primary" @click="stopLoad"></el-button>
            </div>
        </el-dialog>
    </main>
</template>
<script>
    import XLSX from 'xlsx'
    export default{
        data(){
            return {
                fileList:[],
                isUpload:false,
                successNum:0,
                warningNum:0,
                errorNum:0,
                errorIndexArr:[],
                lenMessage:'',
                loadVis:false,
                uploadStatus:false,
                payVis:false,
                isAjax:false,
                isEdit:false,
                tinyAjax:false,
                dialogFormVisible:false,
                picURL:'',
                sameID:false,
                allData:[],
                maxlen:2000,
                multipleSelection:[],
                money:0,
                signFee:0,
                pickerOptions:{
                    disabledDate(time){
                        return time.getTime() >= this.$store.state.sysTime - 8.64e7;
                    }
                },
                rightNum:0,
                allMemberInfo:[],
                memberInfo:{
                    name:'',
                    ID:'',
                    rank:143,
                    isFee:'1',
                    pic:'',
                    birthday:'',
                    gender:1,
                },
                optionGroups:[
                    {
                        label:'',
                        options:[
                            {value:143,label:'28'},
                            {value:142,label:'27'},
                            {value:141,label:'26'},
                            {value:99,label:'25'},
                            {value:103,label:'24'},
                            {value:104,label:'23'},
                            {value:106,label:'22'},
                            {value:107,label:'21'},
                            {value:108,label:'20'},
                            {value:109,label:'19'},
                            {value:110,label:'18'},
                            {value:111,label:'17'},
                            {value:112,label:'16'},
                            {value:113,label:'15'},
                            {value:114,label:'14'},
                            {value:115,label:'13'},
                            {value:116,label:'12'},
                            {value:117,label:'11'},
                            {value:118,label:'10'},
                            {value:119,label:'9'},
                            {value:120,label:'8'},
                            {value:121,label:'7'},
                            {value:122,label:'6'},
                            {value:123,label:'5'},
                            {value:124,label:'4'},
                            {value:125,label:'3'},
                            {value:126,label:'2'},
                            {value:127,label:'1'}
                        ]
                    },
                    {
                        label:'',
                        options:[
                            {value:128,label:'1'},
                            {value:129,label:'2'},
                            {value:130,label:'3'},
                            {value:131,label:'4'},
                            {value:132,label:'5'},
                            {value:133,label:'6'}
                        ]
                    }
                ]
            }
        },
        methods:{
            stay(){
                const that = this;
                that.uploadStatus = false;
                that.rightNum -= that.successNum + that.warningNum;
            },
            clearData(){
                this.allMemberInfo=[];
                this.rightNum = 0;
            },
            toAddPlayer(){
                const that = this;
                that.$parent.addTab('');
                that.$router.push('/Main/addPlayer');
            },
            completeChangeData(){
                const that = this;
                if(that.successNum + that.warningNum + that.errorNum === that.rightNum){
                    let oldArr = [];
                    that.allMemberInfo.forEach((e,i)=>{
                        oldArr.push(e);
                    });
                    that.allMemberInfo = [];
                    that.errorIndexArr.forEach((e,i)=>{
                        that.allMemberInfo.push(oldArr[e]);
                    });
                    that.uploadStatus = true;
                    that.isUpload = false;
                    //  rightNum
                    that.rightNum = 0;
                    that.allMemberInfo.forEach((e,i)=>{
                        if(e.state === 0){
                            that.rightNum++;
                        }
                    });
                }
            },
            continueLoad(){
                const that = this;
                that.formatExcelData(that.allData);
                that.loadVis = false;
            },
            stopLoad(){
                const that = this;
                that.allData = [];
                that.loadVis = false;
            },
            addAllExcelData(){
                const that = this;
                const __url = that.$store.state.total.mem.child.memAdd[1];
                if(__url==='') return false;

                //  rightNum
                that.rightNum = 0;
                that.allMemberInfo.forEach((e,i)=>{
                    if(e.state === 0){
                        that.rightNum++;
                    }
                });
                if(that.rightNum <= 0){
                    that.$message.info('');
                    return false;
                }
                that.successNum = 0;
                that.warningNum = 0;
                that.errorNum = 0;
                that.errorIndexArr = [];
                that.uploadStatus = true;
                that.isUpload = true;
                that.allMemberInfo.forEach((e,i)=>{
                    if(e.state===0){
                        that.$ajax({
                            url:__url,
                            method:'post',
                            params:{
                                idCard:e.ID,
                                name:e.name,
                                level:e.rank,
                                reg_pay:e.isFee,
                                type:1,
                                birthday:e.birthday,
                                sex:e.gender
                            }
                        }).then(res=>{
                            this.dialogFormVisible = false;
                            let _data = res.data;
                            if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                                that.$parent.overTime();
                            }
                            if( _data.msg.includes('') ){
                                that.successNum ++;
                            }else if( _data.msg.includes('') ){
                                that.warningNum ++;
                            }else{
                                that.errorNum ++;
                                that.errorIndexArr.push(i);
                            }
                            // 
                            that.completeChangeData();
                        },res=>{
                            that.errorNum ++;
                            that.errorIndexArr.push(i);
                            this.dialogFormVisible = false;
                            // 
                            that.completeChangeData();
                        });
                    }else{
                        that.errorIndexArr.push(i);
                    }
                });

            },
            uploadExcel(file){
                const that = this;
                let wb;//
                let rABS = false; //
                importExcel(file.file);
                function importExcel(files) {//
                    if(!files) {
                        return false;
                    }
                    const IMPORTFILE_MAXSIZE = 1024 * 3;//
                    const suffix = files.name.split(".")[1];
                    if(suffix !== 'xls' && suffix !=='xlsx'){
                        that.$message.warning('!');
                        return false;
                    }
                    if(files.size/1024 > IMPORTFILE_MAXSIZE){
                        that.$message.warning(' ' + IMPORTFILE_MAXSIZE / 1024 + 'M');
                        return false;
                    }
                    const f = files;
                    const reader = new FileReader();
                    reader.onload = function(e) {
                        const data = e.target.result;
                        if(rABS) {
                            wb = XLSX.read(btoa(fixdata(data)), {//
                                type: 'base64'
                            });
                        } else {
                            wb = XLSX.read(data, {
                                type: 'binary'
                            });
                        }
                        //wb.SheetNames[0]SheetsSheet
                        //wb.Sheets[Sheet]Sheet
                        that.allData =  JSON.parse(JSON.stringify(XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]])).replace(/\s/g,''));
                        if(that.allData.length > 4000){
                            that.loadVis = true;
                            that.maxlen = 4000;
                            that.lenMessage = ''
                        }else if(that.allData.length > 2000){
                            that.loadVis = true;
                            that.maxlen = 2000;
                            that.lenMessage = ''
                        }else{
                            that.formatExcelData(that.allData);
                        }
                    };
                    if(rABS) {
                        reader.readAsArrayBuffer(f);
                    } else {
                        reader.readAsBinaryString(f);
                    }
                }

                function fixdata(data) { // to BinaryString
                    let o = "",
                        l = 0,
                        w = 10240;
                    for(; l < data.byteLength / w; ++l) o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w, l * w + w)));
                    o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)));
                    return o;
                }
            },
            formatExcelData(data){
                const that = this;

                if(data.length===0) return false;
                // Check excel title
                let checkArr = [];
                const rightArr = ['','','',''];
                let rightFlag = true;
                for(let key in data[0]){
                    checkArr.push(key);
                }
                checkArr.forEach( (e,i) => {
                    if( e !== rightArr[i]) rightFlag = false;
                });
                if(!rightFlag){
                    that.$message.warning(',,,');
                    return false;
                }
                // Add data into allMemberInfo
                that.allMemberInfo = [];
                let _state = '';
                let _Info = {};
                that.rightNum = 0;
                data.forEach((e,i)=>{
                    if( e[''] === '' || e[''] === null || typeof e[''] === 'undefined' || !(e[''] === '' || e[''] ==='') || e[''] === '' || e[''] === null || typeof e[''] === 'undefined' || e[''] === '' || e[''] === null || typeof e[''] === 'undefined' ){
                        _state = 2;
                    }else if(!that.$parent.checkID(e[''])[0]){
                        _state = 2;
                    }else{
                        _state = 0;
                        _Info = that.getGenderAndBirthday(e['']);
                        that.rightNum ++;
                    }
                    let _rank = that.getRankVal(e['']);
                    if(!_rank){
                        _rank = '';
                        _state = 2;
                        that.rightNum --;
                    }

                    that.allMemberInfo.push({
                        no       : i+1,
                        ID       : e[''],
                        name     : e[''],
                        rank     : _rank,
                        isFee    : e['']===''?'1':'0',
                        time     : that.$store.state.sysTime.substring(0,16),
                        gender   : _Info.gender,
                        birthday : _Info.birthday,
                        state    : _state,
                        del      : false
                    })
                });
            },
            getGenderAndBirthday(idCard){
                if( idCard.length === 15 ){
                    return { gender : (idCard[14]%2===0)-0+'', birthday : '19'+idCard.substring(6,8) };
                }else if(idCard.length===18){
                    return { gender : (idCard[16]%2===0)-0+'', birthday : idCard.substring(6,14) };
                }
            },
            getRankName(rank){
                rank = rank >>0;
                this.optionGroups.forEach((e,i)=>{
                    e.options.forEach((e,i)=>{
                        if(e.value===rank){
                            rank = e.label;
                            return false;
                        }
                    });
                });
                return rank;
            },
            getRankVal(val){
                val = val.replace(/\s/g,'');
                val = val.replace('','');
                let flag = false;
                this.optionGroups.forEach((e,i)=>{
                    e.options.forEach((e,i)=>{
                        if(e.label===val){
                            val = e.value;
                            flag = true;
                        }
                    });
                });
                if(flag) return val;
                else return false;
            },
            addInfo(){
                this.dialogFormVisible = true;
                this.isEdit = false;
                this.memberInfo={
                    name:'',
                    ID:'',
                    rank:143,
                    isFee:'1',
                    pic:'',
                    birthday:'',
                    gender:''
                };
            },
            checkFormID(id){
                let that = this;
                that.sameID = false;
                that.allMemberInfo.forEach((e,i)=>{
                    if(e.ID===that.memberInfo.ID){
                        that.$message.warning('');
                        that.sameID=true;
                    }
                });
            },
            formatDate(t){
                const _t = new Date(t);
                const f=(a)=>{
                    return a<10?'0'+a:a;
                };
                return _t.getFullYear()+'-'+f(_t.getMonth()+1)+'-'+f(_t.getDate())+' '+f(_t.getHours())+':'+f(_t.getMinutes());
            },
            confirmAdd(formName){
                const that = this;
                that.$refs[formName].validate((valid) => {
                    if(valid){
                        if(that.isEdit){
                            that.edit();
                        }else{
                            that.add();
                        }
                    }
                });
            }
        }
    }
</script>
<style>
    #importPlayer{
        position:relative;
    }
    #importPlayer .text-red{
        color:#ff4949;
        margin-left:16px;
    }
    #importPlayer .text-red:hover{
        opacity:0.7;
    }
    #importPlayer .error{
        color:#ff4949;
        font-size:16px;
    }
    #importPlayer .success{
        color:#13CE66;
        font-size:16px;
    }
    #importPlayer .text-blue{
        color:#20a0ff;
        font-size:14px;
    }
    #importPlayer .text-blue:hover{
        opacity:0.7;
    }
    #importPlayer .warning{
        color:#F7BA2A;
        font-size:16px;
    }
    #importPlayer .tip{
        color:#8492A6;
        font-size:12px;
        margin:4px 0;
        text-align:right;
    }
    #importPlayer .totalNum{
        color:#8492A6;
        font-size:14px;
    }
    #importPlayer .upload-img{
        width:100%;
        height:100%;
    }
    #importPlayer .addMem{
        background:#fff;
        overflow: hidden;
        padding:20px;
    }
    #importPlayer .addMem .head-title p{
        margin:10px 0;
    }
    #importPlayer .add-area{
        margin-bottom:20px;
        position: relative;
    }
    #importPlayer .add-panel{
        padding:15px;
        cursor:pointer;
        border-bottom:1px solid #d3dce6;
    }
    #importPlayer .add-panel:last-of-type{
        border-bottom:0;
    }
    #importPlayer .add-panel .plus{
        display: inline-block;
        width:48px;
        height:48px;
        font-size:68px;
        line-height:38px;
        color:#fff;
        background: #999;
        margin-right:18px;
    }
    #importPlayer .el-icon-delete{
        font-size:22px;
        margin-left:10px;
        cursor:pointer;
    }
    #importPlayer .add-panel .mem-info{
        position: relative;
        top:-16px;
        color:#999;
    }
    #importPlayer .money{
        position: absolute;
        font-size:18px;
    }
    #importPlayer .mem-dialog{
        min-width:500px;
    }
    #importPlayer .mem-dialog .mem-form .form-input{
        width:300px;
    }
    #importPlayer .mem-dialog .mem-form .form-input.age{
        width:100px;
    }
    #importPlayer .refresh{
        display:inline-block;
        margin:10px 0;
    }
    #importPlayer .refresh .fa-refresh{
        color:#20a0ff;
        margin-right:6px;
        cursor:pointer;
    }
    #importPlayer .refresh .fa-refresh:hover{
        opacity:0.7;
    }
    #importPlayer .type-area{
        text-align:center;
    }
    #importPlayer .type-area .wechat,#importPlayer .type-area .ali,#importPlayer .type-area .wallet{
        margin:0 20px 20px 20px;
        cursor:pointer;
        display:inline-block;
    }
</style>


