<template>
<main id="applyFor" class="container">
    <header class="head-title">
        <span>{{isPlan===1?'':''}}</span>
    </header>
    <div style="overflow:hidden">
        <p class="title">{{planTitle}}</p>
       <div class="plan-detail" v-for="item in planData" v-if="!item.content.includes('false')"><span>{{item.title}}</span><span :class="{'text-red':item.content.includes(' ')}">{{item.content}}</span></div>
    </div>
    <div class="applyList">
        <p class="text-gray" v-if="applyList.length===0 && !noData"></p>
        <p class="text-gray" v-if="noData"> <span class="text-blue text-big" @click="toPlan"></span> </p>
        <div class="org-item" v-for="item in applyList">
            <span><i class="fa fa-bank"></i>{{item.name}}</span>
            <span><i class="fa fa-map-marker"></i>{{$parent.getType(item.type)}}</span>
            <span><i class="fa fa-user"></i>{{item.linkMan}}</span>
            <span><i class="fa fa-phone"></i>{{item.linkMob}}</span>
            <span class="arrow-right"><i class="el-icon-arrow-right"></i></span>
            <span class="btn text-g" v-if="item.state===0"></span>
            <span class="check-state text-green" v-if="item.state===2"></span>
            <span class="check-state text-red" v-if="item.state===1"></span>
            <p class="refuse-reason" v-if="item.remark!==null&&item.remark!==''">{{''+item.remark}}</p>
        </div>
    </div>
    <div class="text-center" v-if="isPlan===1&&isConfirm===0">
        <el-button type="primary" @click="payFor" v-if="!isApply"></el-button>
    </div>
    <el-dialog size="tiny" title="" v-model="payVis">
        <div class="type-area">
            <div class="ali" @click="$parent.Pay('ali',orderID,'/Main/myEvent?pay')">
                <img src="./../../assets/img/alipay.png" alt="ali">
                <p></p>
            </div>
            <div class="wechat" @click="$parent.Pay('wx',orderID,'/Main/myEvent?pay')">
                <img src="./../../assets/img/wechat.png" alt="wechat">
                <p></p>
            </div>
            <div class="wallet" @click="" v-if="0">
                <img src="./../../assets/img/walletpay.png" alt="wallet">
                <p></p>
            </div>
        </div>
        <p class="tip"></p>
        <p class="tip"></p>
    </el-dialog>
</main>
</template>
<script>
    export default{
        data(){
            return{
                isApply:false,
                noData:false,
                payVis:false,
                id:'',
                thisID:'',
                orderID:'',
                thisName:'',
                planTitle:'',
                isPlan:'',
                isConfirm:0,
                planData:[],
                applyList:[]
            }
        },
        methods:{
            showTime(e){
                if( e!==null){
                    return e.substring(0,10);
                }else{
                    return false;
                }
            },
            getRange(num){
                if(num===58) return '';
                if(num===59) return '';
                if(num===60) return '';
                if(num===61) return '';
            },
            getData(){
                const that = this;
                const __url = that.$store.state.total.event.child.planDetail[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url,
                    params:{
                        id:that.id
                    }
                }).then(res=>{
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        const event = dat.eventinfo;
                        const org = dat.orginfo;
                        that.planTitle = event.title;
                        that.isPlan = (event.is_plan>>0);
                        that.planData = [
                            {title:'',content:that.showTime(event.apply_start)+'  '+that.showTime(event.apply_end)},
                            {title:'',content:that.showTime(event.game_start)+'  '+that.showTime(event.game_end)},
                            {title:'',content:that.getRange(event.scale>>0)},
                            {title:'',content:(event.bail===null?0:(event.bail-0).toFixed(2))+' '},
                            {title:':',content:org.length.toString()}
                        ];
                        org.forEach((e,i)=>{
                            that.applyList.push({
                                id:e.org_id,
                                state:(e.check_state>>0),
                                name:e.org_name,
                                type:e.org_level,
                                linkMan:e.link_name,
                                linkMob:e.link_phone,
                                remark:e.remark
                            });
                            if(e.org_id===that.$store.state.orgId){
                                that.isApply = true;
                            }
                            if(e.check_state>>0===2){
                                that.isConfirm = 1;
                            }
                        });
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                   that.$message.warning('');
                });
            },
            toPlan(){
                this.$parent.addTab('');
                this.$router.push('/Main/plan');
            },
            payFor(){
                const that = this;
                const __url = that.$store.state.total.event.child.planApply[1];
                if(__url==='') return false;
                that.$ajax({
                    url:that.$store.state.total.event.child.planApply[1],
                    params:{
                        id:that.id
                    }
                }).then(res=>{
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        if((dat.state>>0)===0){
                            that.orderID = dat.orderID;
                            that.payVis = true;
                        }else if((dat.state>>0)===2){
                            that.$router.push('/Main/myEvent?free');
                            that.$parent.addTab('');
                        }
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                   that.$message.warning('');
                });
            },
            watch(id){

            }
        },
        mounted(){
            const that = this;
            const href = window.location.href;
            const index = href.indexOf('?');
            that.id = parseInt(href.substring(index+1,index+5));
            if(that.id){
                that.getData();
            }else{
                that.noData = true;
            }
        }
    }
</script>
<style>
    #applyFor{
        background:#fff;
        padding:0 20px;
    }
    #applyFor .text-gray{
        margin-top:10px;
        color:#8492A6;
        font-size:16px;
        text-align:center;
    }
    #applyFor .text-blue{
        color:#20a0ff;
        font-size:14px;
    }
    #applyFor .text-big{
        font-size:18px;
        cursor: pointer;
    }
    #applyFor .text-big:hover{
        opacity:0.7;
    }
    #applyFor .text-center{
        margin-top:30px;
    }
    #applyFor .title{
        color:#20a0ff;
        margin-bottom:14px;
    }
    #applyFor .fa{
        margin-right:4px;
    }
    #applyFor .plan-detail{
        font-size:14px;
        float:left;
        width:50%;
        margin-bottom:14px;
    }
    #applyFor .plan-detail span:first-child{
        width:104px;
        display: inline-block;
    }
    #applyFor .text-red {
        color: #ff4949;
    }
    #applyFor .text-green{
        color: #13ce66;
    }
    #applyFor .applyList .org-item{
        border-top:1px solid #ddd;
        min-height:80px;
    }
    #applyFor .applyList .org-item:last-child{
        border-bottom:1px solid #ddd;
    }
    #applyFor .applyList .org-item>span{
        display: inline-block;
        overflow: hidden;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
        white-space: nowrap;
        line-height:80px;
    }
    #applyFor .applyList .org-item>span:first-child{
        max-width:200px;
        width:20%;
    }
    #applyFor .applyList .org-item>span:nth-child(2){
        max-width:130px;
        width:14%;
    }
    #applyFor .applyList .org-item>span:nth-child(3){
        max-width:150px;
        width:12%;
    }
    #applyFor .applyList .org-item>span:nth-child(4){
        max-width:120px;
        width:18%;
    }
    #applyFor .applyList .org-item>span:nth-child(5){
        max-width:85px;
        width:9%;
        text-align: center;
    }
    #applyFor .applyList .org-item>span:nth-child(6),#applyFor .applyList .org-item>span:nth-child(7){
        text-align: center;
    }
    #applyFor .btn{
        margin:0 10px;
    }
    #applyFor .arrow-right{
        color:#20a0ff;
    }
    #applyFor .text-g{
        color:#8492A6;
    }
    #applyFor .el-button{
        min-width:68px;
    }
    #applyFor .tip{
        color:#8492A6;
        font-size:12px;
    }
    #applyFor .type-area{
        text-align:center;
    }
    #applyFor .type-area .wechat,#applyFor .type-area .ali,#applyFor .type-area .wallet{
        margin:0 20px 20px 20px;
        cursor:pointer;
        display:inline-block;
    }
    #applyFor .refuse-reason{
        color:#999;
        margin-bottom:10px;
    }
</style>


