<template>
    <div>
        <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/link" xml:space="preserve" width="100%" height="600" v-for="item in list">
            <polyline points="0 0,1600 0,1600 600,0 600" class="main" fill="url(../../assets/img/jiwei_max.jpg)"/>
            <text x="600" y="200" fill="#666" font-family="sans-serif" font-size="16px">{{item.name}}</text>
            <text x="900" y="400" fill="#666" font-family="sans-serif" font-size="16px">{{item.rank}}</text>
            <text x="1200" y="560" fill="#666" font-family="sans-serif" font-size="16px">2017931</text>
    </svg>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                list:[
                    {name:'1',rank:'1'},
                    {name:'2',rank:'2'},
                    {name:'3',rank:'3'},
                    {name:'4',rank:'4'},
                    {name:'5',rank:'5'},
                    {name:'6',rank:'6'},
                    {name:'7',rank:'7'},
                    {name:'8',rank:'8'},
                    {name:'9',rank:'9'},
                    {name:'10',rank:'10'},
                    {name:'11',rank:'11'},
                ]
            }
        },
        mounted(){
            window.print();
        }
    }
</script>


