<template>
    <main id="main"
          :loading.body="bodyLoading">
        <!--  -->
        <header class="header">
            <div>
                
            </div>
            <!---->
            <!--<span class="system-time">{{showTime($store.state.sysTime)}}</span>-->
            <div>
                <el-button type="text"
                           v-if="$store.state.bindState==='0'"
                           @click="toBindYunBiSai"></el-button>
                <span @click="more()"
                      style="cursor: pointer;margin-right:30px">
                        <el-badge :value="msgNum"
                                  :max="99"
                                  class="item">
                        <span class="fa fa-bell"></span>
                    </el-badge>
                    </span>
                <!--<span class="fa fa-user-circle headImg"></span>-->
                <span class="userName"
                      @click="showUserInfo">
                    {{orgName}} - {{userName}}
                </span>
                <span class="exit"
                      @click="exit"></span>
            </div>
        </header>
        <main>
            <!--  -->
            <div class="main-left">
                <el-row>
                    <el-menu default-active="/Organization"
                             class="el-menu-vertical"
                             theme="dark"
                             :router="true"
                             :unique-opened="true">
                        <el-menu-item class="slide-menu"
                                      :index="homePath"
                                      @click="addTab($event)"
                                      :key="homePath"
                                      v-if="$store.state.total.home.auth">
                            <router-link to="/Main/home">
                                <div class="inner-img icon-home"></div>
                                
                            </router-link>
                        </el-menu-item>
                        <el-menu-item class="slide-menu"
                                      :index="orgPath"
                                      :key="orgPath"
                                      @click="addTab($event)"
                                      v-if="$store.state.total.org.auth">
                            <router-link to="/Main/org">
                                <div class="inner-img icon-org"></div>
                                
                            </router-link>
                        </el-menu-item>
                        <el-submenu  :index="index"
                                     :key="item.icon"
                                     v-for="(item,index) in Modules"
                                     v-if="$store.state.total[item.icon].auth">
                            <template slot="title">
                                <div class="inner-img"
                                     :class="'icon-'+item.icon"></div>{{item.name}}
                            </template>
                            <el-menu-item-group>
                                <el-menu-item :key="child.path"
                                              @click="addTab($event)"
                                              :index="child.path"
                                              v-for="child in item.children"
                                              v-if="$store.state.subAuth[child.name].auth">{{child.name}}</el-menu-item>
                            </el-menu-item-group>
                        </el-submenu>
                    </el-menu>
                </el-row>
            </div>
            <!--  -->
            <div class="main-right">
                <div class="tab">
                    <div class="tab-item"
                         v-model="item.name"
                         :class="{'tab-active':item.active}"
                         @click="routeGo($event,item.name)"
                         v-for="item in editableTabs">
                        {{item.name}}
                        <span class="tab-close"
                              v-if="item.closeable"
                              @click.stop="removeTab($event)"></span>
                    </div>
                </div>
                <div class="right-wrap">
                    <transition name="el-fade-in-linear">
                        <router-view class="view"></router-view>
                    </transition>
                </div>
            </div>
        </main>
        <el-dialog title=""
                   :visible.sync="infoVisible">
            <div class="info"
                 v-for="item in userInfo">
                <span class="title">{{item.title}}</span>
                <span class="detail"
                      v-html="item.detail">{{item.detail}}</span>
            </div>
            <div class="text-center">
                <el-button type="info"
                           @click="toPassword"></el-button>
                <el-button type="primary"
                           @click="toOrgEdit"></el-button>
            </div>
        </el-dialog>
        <el-dialog type="small"
                   title=""
                   :visible.sync="pwdState"
                   :model="change"
                   ref="change"
                   :rules="changeRules"
                   :close-on-click-modal="false"
                   :close-on-press-escape="false"
                   :show-close="false">
            <el-form label-position="left"
                     label-width="100px">
                <el-form-item prop="newPwd" label="">
                    <el-input placeholder=""
                              :minlength="6"
                              :maxlength="18"
                              type="password"
                              v-model="change.newPwd"></el-input>
                </el-form-item>
                <el-form-item prop="repeatPwd"
                              label="">
                    <el-input placeholder=""
                              :minlength="6"
                              :maxlength="18"
                              type="password"
                              v-model="change.repeatPwd"></el-input>
                </el-form-item>
                <el-form-item class="text-center">
                    <el-button size="small"
                               type="primary"
                               @click="changePwd"> </el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <el-dialog type="small"
                   title=""
                   :visible.sync="authState"
                   :model="change" ref="change"
                   :rules="changeRules"
                   :close-on-click-modal="false"
                   :close-on-press-escape="false"
                   :show-close="false">
            <p></p>
            <div class="text-center"
                 style="margin-top:10px">
                <el-button @click="authState = false"></el-button>
                <el-button type="primary"
                           @click="completePic"></el-button>
            </div>
        </el-dialog>
    </main>
</template>
<script>
    export default {
        name: 'app',
        data(){
            return {
                change:{
                    oldPwd:'123456',
                    newPwd:'',
                    repeatPwd:''
                },
                changeRules:{
                    oldPwd:[
                        {required:true,message:'',trigger:'blur'},
                        {min:3,max:18,message:'6-18',trigger:'blur'}
                    ],
                    newPwd:[
                        {required:true,message:'',trigger:'blur'},
                        {min:6,max:18,message:'6-18',trigger:'blur'},
                    ]
                },
                pwdState:false,
                authState:false,
                bodyLoading:true,
                infoVisible:false,
                homeInfo:{},
                orgName:'',
                userName:'',
                msgNum: 5,
                userInfo:[],
                Modules: {
                    member: {
                        name: '',
                        icon: 'mem',
                        children: [
                            {name: '', path: '/Main/player'},
                            {name: '', path: '/Main/transferList'},
                            {name: '', path: '/Main/judge'},
                            {name: '', path: '/Main/coach'},
                        ]
                    },
                    event: {
                        name: '',
                        icon: 'event',
                        children: [
                            {name: '', path: '/Main/plan'},
                            {name: '', path: '/Main/myEvent'},
                        ]
                    },
                    credential: {
                        name: '',
                        icon: 'cre',
                        children: [
                            {name: '', path: '/Main/wait'},
                            {name: '', path: '/Main/apply'},
                            {name: '', path: '/Main/send'}
                        ]
                    },
                    wallet: {
                        name: '',
                        icon: 'wallet',
                        children: [
                            {name: '', path: '/Main/WCash'},
                            {name: '', path: '/Main/WTransfer'},
                            {name: '', path: '/Main/WToYunbisai'},
                            {name: '/', path: '/Main/WCreate'},
                            {name: '', path: '/Main/WList'}
                        ]
                    },
                    statistics: {
                        name: '',
                        icon: 'sta',
                        children: [
                            {name: '', path: '/Main/charts'}
                        ]
                    },
                    authority: {
                        name: '',
                        icon: 'set',
                        children: [
                            {name: '', path: '/Main/setting1'},
                            {name: '', path: '/Main/setting2'},
                            {name: '', path: '/Main/setting3'}
                        ]
                    }
                },
                allPath:[
                    ['/Main',''],
                    ['/Main/more',''],
                    ['/Main/org',''],
                    ['/Main/OrgAdd',''],
                    ['/Main/OrgEdit',''],
                    ['/Main/player',''],
                    ['/Main/transferPlayer',''],
                    ['/Main/transferList',''],
                    ['/Main/addPlayer',''],
                    ['/Main/importPlayer',''],
                    ['/Main/judge',''],
                    ['/Main/addJudge',''],
                    ['/Main/coach',''],
                    ['/Main/addCoach',''],
                    ['/Main/plan',''],
                    ['/Main/planApply',''],
                    ['/Main/planAdd',''],
                    ['/Main/planEdit',''],
                    ['/Main/applyFor',''],
                    ['/Main/myEvent',''],
                    ['/Main/wait',''],
                    ['/Main/apply',''],
                    ['/Main/send',''],
                    ['/Main/WCash',''],
                    ['/Main/WTransfer',''],
                    ['/Main/WCreate','/'],
                    ['/Main/WList',''],
                    ['/Main/charts',''],
                    ['/Main/setting1',''],
                    ['/Main/setting2',''],
                    ['/Main/setting3','']
                ],
                homePath: '/Main/home',
                orgPath: '/Main/org',
                systemTime:'',
                editableTabs: [{
                    name: '',
                    active:true,
                    closeable:false
                }],
                tabIndex: 1
            }
        },
        methods: {
            openNewTag(){
                this.$notify.info({title:'',message:''});
            },
            completePic(){
                this.$router.push('/Main/orgEdit?'+this.$store.state.orgId);
                this.authState = false;
                this.addTab('');
            },
            changePwd(){
                const that = this;
                const __url = that.$store.state.total.set.child.childChangePwd[1];
                if(that.change.newPwd === ''){
                    that.$message.warning('');
                    return false;
                }
                if(that.change.repeatPwd === ''){
                    that.$message.warning('');
                    return false;
                }
                if( that.change.repeatPwd.length > 18 || that.change.newPwd.length > 18 || that.change.repeatPwd.length < 6 || that.change.newPwd.length < 6 ){
                    that.$message.warning(' 6-18 ');
                    return false;
                }
                if( that.change.newPwd !== that.change.repeatPwd ){
                    that.$message.warning('');
                    return false;
                }
                if(__url==='') return false;
                that.tinyAjax = true;
                that.$ajax({
                    url:__url,
                    params:{
                        u_id:that.$store.state.userId,
                        oldpwd:that.change.oldPwd,
                        newpwd:that.change.newPwd,
                    }
                }).then(res=>{
                    that.tinyAjax = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.pwdState =false;
                        that.$notify.success({
                            title: '',
                            message:'',
                            duration: 0,
                            offset:80
                        });
                        document.cookie = 'state=1';
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                    that.tinyAjax = false;
                    that.pwdState =false;
                    that.$message.warning('');
                });

            },
            toBindYunBiSai(){
                this.addTab('');
                this.$router.push('/Main/setting3');
            },
            getLocation(){
              this.$ajax({
                  url:'http://int.dpool.sina.com.cn/iplookup/iplookup.php'
              }).then();
            },
            Pay(type,orderID,url){
                const that = this;
                let pay_url = '';
                if(type==='ali') pay_url = that.$store.state.pay.ali;
                else if(type==='wx') pay_url = that.$store.state.pay.wx;
                window.location.href =  that.$store.state.baseURL + pay_url + '?orderid=' + orderID + '&type=1&return_url=http://' + window.location.host + '/#' + url ;
            },
            toPassword(){
                this.$router.push('/Main/setting1');
                this.addTab('');
                this.infoVisible = false;
            },
            toOrgEdit(){
                this.$router.push('/Main/orgEdit?'+this.$store.state.orgId);
                this.addTab('');
                this.infoVisible = false;
            },
            showTime(time){
                const f = (a)=>{return a<10?'0'+a:a};
                time = new Date(time);
                let y = time.getFullYear(),
                    M = time.getMonth()+1,
                    d = time.getDate(),
                    h = time.getHours(),
                    m = time.getMinutes(),
                    s = time.getSeconds();
                return y+''+f(M)+''+f(d)+' '+f(h)+':'+f(m)+':'+f(s);
            },
            overTime(){
                this.$message.warning('');
                this.$router.push('/login');
            },
            getType(num){
                if(num==='1') return '';
                else if(num==='2') return '';
                else if(num==='3') return '';
                else if(num==='4') return '';
            },
            showPic(src){
                if(src===null) return '';
                const that = this;
                const arr = src.split(',');
                let content = '';
                arr.forEach((e,i)=>{
                     content += '<img src="'+that.$store.state.imgURL+e+'" alt="'+i+'"/>';
                });
                return content;
            },
            showUserInfo(){
                const that = this;
                const __url = that.$store.state.total.org.child.orgDetail[1];
                if(__url==='') return false;
                that.infoVisible = true;
                that.$ajax({
                    url:__url
                }).then(res=>{
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        const area = that.cityQuery(dat.county,'county','get');
                        that.userInfo = [
                            {title:'',detail:that.homeInfo.userName},
                            {title:'',detail:that.getType(dat.org_level)},
                            {title:'',detail:dat.legal_name},
                            {title:'',detail:dat.link_name},
                            {title:'',detail:dat.link_phone},
                            {title:'',detail:area[0]+area[1]+area[2] + dat.address === null ? '' : dat.address},
//                            {title:'',detail:that.showPic(dat.lease_img)},
//                            {title:'',detail:that.showPic(dat.place_img)},
//                            {title:'',detail:that.showPic(dat.license_img)}
                        ];
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.overTime();
                    }else{
                        that.$message.warning(data.msg);
                    }
                },res=>{
                    that.$message.warning('');
                });
            },
            setCookie(arr){
                arr.forEach((e,i)=>{
                    document.cookie = e.name +'='+escape(e.val);
                });
            },
            getCookie(name){
                let res='';
                document.cookie.split('; ').forEach((e,i)=>{
                    const arr = e.split('=');
                    if(arr[0]===name){
                        res = unescape(arr[1]);
                        return false;
                    }
                });
                return res;
            },
            delCookie(name){
                const date = new Date();
                date.setTime(date.getTime() - 10000);
                document.cookie = name + '=vvv;expires=' + date.toGMTString();
            },
            getHomeInfo(){
                const that = this;
                const __url = that.$store.state.total.home.child.homeInfo[1];
                if(__url==='') return false;
                that.bodyLoading = true;
                //
                that.$ajax({
                    method:'get',
                    url:__url
                }).then((res)=>{
                    that.bodyLoading = false;
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        that.orgName = dat.org_name;
                        that.userName = dat.user_name===dat.org_name?'':dat.user_name;
                        that.msgNum = dat.notices_num;
                        that.homeInfo = {
                            orgName:dat.org_name,
                            userName:dat.user_name,
                            memberNum:dat.mem_num,
                            yesterdayMemberNum:dat.yes_mem_num,
                            weekMemberNum:dat.week_mem_num,
                            orgNum:dat.org_num,
                            yesterdayOrgNum:dat.yes_org_num,
                            weekOrgNum:dat.week_org_num,
                            eventNum:dat.event_num,
                            yesterdayEventNum:0,
                            myEvent:dat.event_num_self,
                            weekEventNum:0,
                            sysInfo:dat.notices,
                            orgId:dat.org_id,
                            userId:dat.user_id
                        };
                        that.systemTime = dat.sys_time;

                        //   
                        if(dat.org_level==='1'){
                            that.Modules.credential.children = [
                                {name: '', path: '/Main/apply'},
                                {name: '', path: '/Main/send'}
                            ];
                            that.Modules.event.children.forEach((e,i)=>{
                                if(e.name===''){
                                    that.Modules.event.children.splice(i,1);
                                }
                            });
                        }else if(dat.org_level==='4'){
                            that.$store.state.total.org.auth = false;
                        }
                        // 
                        const __obj = {
                            all:[that.homeInfo.orgNum,that.homeInfo.memberNum,that.homeInfo.eventNum],
                            yesterday:[that.homeInfo.yesterdayOrgNum,that.homeInfo.yesterdayMemberNum,that.homeInfo.eventNum],
                            week:[that.homeInfo.weekOrgNum,that.homeInfo.weekMemberNum,that.homeInfo.eventNum],
                        };
                        that.$store.commit('setNumber',__obj);
                        that.$store.commit('setMyEvent',that.homeInfo.myEvent);
                        // 
                        that.$store.commit('setInfo',{
                            orgId:that.homeInfo.orgId,
                            bindState:dat.lswl_org_id===null?0:1,
                            orgLevel:(dat.org_level>>0),
                            orgName:that.homeInfo.orgName,
                            pro:dat.province,
                            sysTime:that.systemTime,
                            userId:that.homeInfo.userId
                        });
                        // 
                        const __state = dat.auth_state;
                        if( __state.lease_img==='' || __state.lease_img === null || __state.place_img === '' || __state.place_img === null || __state.license_img === '' || __state.license_img === null){
                            that.authState = true;
                        }
                        // 
                        that.$store.commit('updateTime');
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },(res)=>{
                    that.bodyLoading = false;
                    that.$message.warning('');
                });
            },
            addTab(target, app){
                let tabName;
                let that = app || this;
                let same =  false;
                typeof target === 'string' ? (tabName = target) : (tabName = target.$el.innerText);
                let open = that.editableTabs;
                open.forEach((e,i)=>{
                    e.active = false;
                    if(e.name === tabName){
                        e.active = true;
                        same = true;
                    }
                });
                if(same) return false;
                if (open.length > 5) {
                    that.removeTab(open[1].name);
                }
                that.editableTabs.push({
                    name:tabName,
                    active:true,
                    closeable:true
                });
            },
            removeTab(event){
                const _text = (typeof event==='string') ? event : (event.target.parentNode.innerText.replace(/\s/g,''));
                let that = this;
                let tabs = that.editableTabs;
                tabs.forEach((e,i)=>{
                    if(e.name===_text){
                        if(i===tabs.length-1||e.active){
                            tabs[i-1].active=true;
                            that.routeGo(tabs[i-1].name,tabs[i-1].name);
                        }
                        tabs.splice(i,1);
                    }
                });
            },
            searchRoute(event,name){
                const nameArr = ['','','','','','','','','','','','','',''],

                    pathArr = ['/Main','/Main/org','/Main/addPlayer','/Main/OrgAdd','/Main/more','/Main/OrgAdd','/Main/planApply','/Main/planAdd','/Main/applyFor','/Main/eventApply','/Main/planEdit','/Main/OrgEdit','/Main/importPlayer','/Main/transferPlayer'],
                      eventName = typeof event === 'string' ? event : event.target.innerText;
                let flag = false;
                this.editableTabs.forEach((e,i)=>{
                    e.active = eventName===this.editableTabs[i].name;
                });
                nameArr.forEach((e,i)=>{
                   if(name===e){this.$router.push(pathArr[i]);return false;}
                   if(i>=nameArr.length-1) flag = true;
                });
                if(flag){
                    let _list = this.Modules;
                    for (let i in _list) {
                        let _inner = _list[i].children;
                        for (let j in _inner) {
                            if (_inner[j].name === name) {
                                this.$router.push(_inner[j].path);
                            }
                        }
                    }
                }
            },
            routeGo(event,name){
                this.searchRoute(event,name.replace(/\s/g, ''));
            },
            more(){
                this.msgNum = 0;
                this.addTab('');
                this.$router.push('/Main/more');
            },
            exit(){
                const that =this;
                document.cookie.split('; ').forEach((e,i)=>{
                    const arr = e.split('=');
                    that.delCookie(arr[0]);
                });
                this.$router.push('');
                const host = window.location.host;
                window.location.href = 'http://' + host;
            },
            getToken(){
                let getCookie = (name)=>{
                    let arrCookie = document.cookie.split('; ');
                    for(let i = 0; i < arrCookie.length; i++){
                        let arr = arrCookie[i].split('=');
                        if( arr[0] === name ) return arr[1];
                    }
                    return '';
                };
                return getCookie('token');
            },
            refreshToken(){
                const that = this;
                const __url = that.$store.state.refreshToken[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url
                }).then( res => {
                    const _data = res.data.token;
                    document.cookie = 'token='+_data;
                    that.$ajax.defaults.headers={token:_data};
                },res=>{
                    that.$message.warning('');
                });
            },
            openTab(href){
                const that = this;
                that.allPath.forEach((e,i)=>{
                    if(href.includes(e[0])){
                        that.addTab(e[1]);
                        return false;
                    }
                });
            },
            checkID(code) {
                let city = {11: "",12: "",13: "",14: "",15: "",21: "",22: "",23: " ",31: "",32: "",33: "",34: "",35: "",36: "",37: "",41: "",42: " ",43: "",44: "",45: "",46: "",50: "",51: "",52: "",53: "",54: " ",61: "",62: "",63: "",64: "",65: "",71: "",81: "",82: "",91: " "};
                let tip = "";
                let pass = true;
                if(code!==''){
                    code = code.toUpperCase();
                }
                if (!code || !/^(^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$)|(^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[Xx])$)$/.test(code)) {
                    tip = "";
                    pass = false;
                }
                else if (!city[code.substr(0, 2)]) {
                    tip = "";
                    pass = false;
                }
                else {
                    //18
                    if (code.length === 18) {
                        code = code.split('');
                        //(aiWi)(mod 11)
                        //
                        let factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
                        //
                        let parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2];
                        let sum = 0;
                        let ai = 0;
                        let wi = 0;
                        for (let i = 0; i < 17; i++) {
                            ai = code[i];
                            wi = factor[i];
                            sum += ai * wi;
                        }
                        let last = parity[sum % 11];
                        if (parity[sum % 11] != code[17]) {
                            tip = ""; // 
                            pass = false;
                        }
                    }
                }
                return [pass, tip];
